export function getContent(languageCode: string): LanguageContent {
  return multilingualContent[languageCode] || multilingualContent.en
}

export function getLanguageDisplayName(code: string): string {
  const names: Record<string, string> = {
    en: "English",
    hi: "Hindi (हिंदी)",
    or: "Odia (ଓଡ଼ିଆ)",
    bn: "Bengali (বাংলা)",
    te: "Telugu (తెలుగు)",
    ta: "Tamil (தமிழ்)",
    mr: "Marathi (मराठी)",
    gu: "Gujarati (ગુજરાતી)",
  }
  return names[code] || "English"
}
